import React from "react";
import { Link } from "react-router-dom";
import { Box, Typography, useMediaQuery, useTheme } from "@mui/material";
import calendar from "../../assets/images/news/calendar.svg";
import button from "../../assets/images/news/button.svg";
import { makeStyles } from "@mui/styles";

const Card = ({ item, i18n }) => {
  const classes = useStyles();
  const theme = useTheme();
  const mobile = useMediaQuery(theme.breakpoints.down(576));
  const tablet = useMediaQuery(theme.breakpoints.down(992));

  return (
    <Link to={`/main/news/${item?.id}`}>
      <Box
        flexWrap="nowrap"
        borderBottom="1px solid #D0D5DD"
        pb={3}
        mb={2.5}
        className={classes.gridItem}
        display="flex"
        flexDirection={{ xs: "column", md: "row" }}
      >
        <div className="img">
          <img src={item?.photo} alt="" />
        </div>
        <Box
          display="flex"
          width="100%"
          flexWrap="nowrap"
          justifyContent="space-between"
        >
          <div>
            {!mobile && (
              <Typography
                variant="body2"
                color="#64748B"
                display="flex"
                alignItems="center"
                mt={2.5}
              >
                <img src={calendar} style={{ marginRight: 10 }} alt="" />
                {item?.date}
              </Typography>
            )}
            <Typography
              variant="h3"
              sx={{
                textOverflow: "ellipsis",
                overflow: "hidden",
                display: "-webkit-box",
                "-webkit-line-clamp": '2',
                "-webkit-box-orient": "vertical",
                wordWrap: "break-word",
                lineHeight: "37px",
                m: { xs: "9px 0 8px", md: "16px 0 17px" },
              }}
            >
              {item?.name}
            </Typography>
            {!mobile && (
              <Typography
                variant="body2"
                sx={{
                  textOverflow: "ellipsis",
                  overflow: "hidden",
                  display: "-webkit-box",
                  "-webkit-line-clamp": '2',
                  "-webkit-box-orient": "vertical",
                  wordWrap: "break-word",
                  lineHeight: "22px",
                }}
              >
                {item?.body}
              </Typography>
            )}
          </div>
          {!mobile && (
            <img
              src={button}
              style={
                tablet ? { height: 150, marginLeft: 16 } : { marginLeft: 16 }
              }
              alt=""
            />
          )}
        </Box>
      </Box>
    </Link>
  );
};

export default Card;

const useStyles = makeStyles((theme) => ({
  gridItem: {
    "&:hover": {
      "& .img img": {
        transform: "scale(1.1)",
      },
    },

    "& .img": {
      width: 288,
      minWidth: 288,
      height: 192,
      borderRadius: 8,
      overflow: "hidden",
      marginRight: 32,
      "& img": {
        width: "100%",
        transition: "all .2s linear",
        height: "100%",
        objectFit: "cover",
      },

      [theme.breakpoints.down(992)]: {
        width: 150,
        minWidth: 150,
        height: 150,
        marginRight: 13,
      },

      [theme.breakpoints.down(576)]: {
        width: "100%",
        height: 190,
        minWidth: "100%",
        marginRight: 13,
      },
    },
  },
}));
