import { Box, Container, Paper, Typography } from "@mui/material";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import NewsCard from "../../components/NewsCard";
import { getNews } from "../../redux/reducers/infoReducer";
import { courses, MBox } from "../../shared/animations";

const News = ({ t }) => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getNews());
    document.title = t("allNewsTitle");
    // eslint-disable-next-line
  }, []);
  const { newsList } = useSelector(({ infoReducer }) => infoReducer);
  const arrayNews = Object.values(
    typeof newsList?.news === "object" && newsList?.news
  );
  return (
    <Paper style={{ padding: "80px 0" }}>
      <Container maxWidth="lg">
        <Typography variant="h2" style={{ marginBottom: 40 }}>
          {t("allNewsTitle")}
        </Typography>
        {arrayNews.map((item, idx) => (
          <MBox
            custom={idx + 1}
            variants={courses}
            initial="hidden"
            whileInView="visible"
            key={idx}
            viewport={{ once: true }}
          >
            <NewsCard item={item} key={idx} />
          </MBox>
        ))}
      </Container>
    </Paper>
  );
};

export default News;
